@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap);
* {
  box-sizing: border-box;
  color: #e4e4e4;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */    /* Konqueror */      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;    
}

body {
  background-color: #202020;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.transitions {
  transition: 0.3s;
}

header {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 50px;
    background-color: #444444;
    font-family: "Gloria Hallelujah";
    font-size: 18px;
}

header h1 {
    color: #fead18;
}

nav {
    display: flex;
    align-items: center;
    background: linear-gradient(0deg, #444444, #555555);
    height: 25px;
    font-size: 14px;
}

nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

nav ul li {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    height: 25px;
}

nav ul li:hover {
  background-color: #666666;
  border-radius: 3px;
  color: #fead18;
}

main {
    height: calc(100vh - 110px);
}

footer {
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    background-color: #444444;
}

.panel-menu {
    display: flex;
    background-color: #444444;
    flex-direction: column;
    position: fixed;
    display: flex;
    top: 75px;
    z-index: 1;
    border-radius: 3px;
    box-shadow: 6px 6px 23px -10px rgba(0, 0, 0, 0.5);
}

.panel-menu li:hover {
    border-radius: 3px;
}
aside {
    position: absolute;
    top: 80px;
    left: 5px;
    width: 60px;
    font-size: 10px;
    color: black;
    background-color: #444444;
    box-shadow: 6px 6px 23px -10px rgba(0, 0, 0, 0.3);
    border-radius: 3px;
}

.panel-header {
    border-radius: 3px 3px 0 0;
    display: flex;
    align-items: center;
    padding: 5px;
    background-color: #363636;
    cursor: move;
}

.panel-tools-grid {
    display: grid;
    grid-template-columns: 30px 30px;
    border-radius: 3px;
}

.panel-tools-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    background-color: #444444;
    border-radius: 3px;
}

.panel-tools-btn:hover {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    background-color: #525252;
}
